.one {
  background: #252525;
}

.two {
  background: #202020;
}

.three {
  background: #1b1b1b;
}

.four {
  background: #161616;
}

.clearfix::before, .clearfix::after {
  display: table;
  content: ' ';
}

.clearfix::after {
  clear: both;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.hidden {
  display: none !important;
}

.visible,
.block {
  display: block !important;
}

.inline-block {
  display: inline-block !important;
}

.inline {
  display: inline !important;
}

.padding {
  padding: 1rem;
}

.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.text-center {
  text-align: center;
}

/* --------------------------------------------------

   Breakpoint Mixin
   -------------------------------------------------- */
*, *::before, *::after {
  box-sizing: inherit;
}

*, html {
  box-sizing: border-box;
}

body {
  font-family: "Open Sans";
  color: #a19f9e;
  background-color: #2a2a2a;
  position: relative;
  line-height: 1.3rem;
}

img {
  box-sizing: content-box;
  width: 100%;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "belligerent";
}

.nostyle {
  color: inherit;
  text-decoration: none;
}

strong, em, small, code, hr {
  line-height: 1;
}

strong {
  font-weight: 600;
}

em {
  font-style: italic;
}

a {
  color: #80b6d3;
  text-decoration: none;
  word-wrap: break-word;
}

sup, sub {
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -.4em;
}

sub {
  bottom: -.25em;
}

blockquote {
  padding-left: 1rem;
  color: #888584;
  border-left: 0.2rem solid #a19f9e;
}

blockquote > *:last-child {
  margin-bottom: 0;
}

code {
  font-family: monospace, "hackregular";
  white-space: nowrap;
}

pre code {
  white-space: pre-wrap;
}

.article__header {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

aside {
  position: relative;
  padding: 1rem;
}

aside .blog__pagination,
aside .blog__tags {
  text-align: center;
}

.article__source a {
  display: block;
  text-align: left;
  color: #a19f9e;
}

.article__source a:hover {
  color: #bab9b8;
  text-decoration: underline;
}

.article__source a:before {
  content: "- ";
}

.article__gallery img {
  padding: 1rem 0px;
}

header.main {
  width: 100%;
}

@media (min-width: 1024px) {
  header.main {
    display: flex;
  }
}

header.main figure {
  padding: 0;
}

header.main figure img {
  width: 115px;
  height: 110px;
  overflow: hidden;
  margin: 0px auto;
  display: block;
}

@media (min-width: 1024px) {
  header.main figure img {
    margin: 0;
  }
}

@media (min-width: 1024px) {
  header.main figure {
    width: 30%;
  }
}

@media (min-width: 1024px) {
  main.grid {
    display: flex;
  }
}

@media (min-width: 1024px) {
  main.grid section, main.grid article, main.grid aside {
    width: 50%;
  }
}

@media (min-width: 1024px) {
  main.grid aside {
    text-align: center;
  }
}

nav {
  text-align: center;
  padding: 1rem 0px;
}

@media (min-width: 1024px) {
  nav {
    text-align: right;
    width: 80%;
    padding: 3rem 0px;
  }
}

nav .menu__active {
  color: #bab9b8;
  text-decoration: none;
}

nav .menu__active svg {
  display: none;
}

nav a {
  color: #80b6d3;
  padding-right: 1rem;
}

nav a:last-child {
  padding-right: 0rem;
}

nav a:hover {
  color: #a6cce0;
}

@media (min-width: 1024px) {
  nav a {
    font-size: 2rem;
    padding-right: 1.5rem;
  }
}

nav .icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: #80b6d3;
  fill: #80b6d3;
}

nav .icon:hover {
  stroke: #a6cce0;
  fill: #a6cce0;
}

.blog__header {
  position: relative;
  color: white;
}

.blog__header .icon {
  padding-top: .5rem;
  stroke: white;
  fill: white;
}

.blog__header img {
  background-size: cover;
  background-position: 50%;
  width: 100%;
  max-height: 500px;
}

.blog__header span:last-child {
  text-transform: lowercase;
}

.cover img {
  background-size: cover;
  background-position: 50%;
  width: 100%;
  max-height: 500px;
}

.brands {
  text-align: center;
}

.brands img {
  width: 150px;
  height: auto;
  padding: 1rem;
}

.brands img:last-child {
  padding-right: 0;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 1rem;
}

.cards a {
  color: #a19f9e;
  text-decoration: none;
}

.cards a:hover {
  opacity: 0.5;
}

.cards .card {
  border: thin solid #111111;
  border-radius: .5rem;
  box-shadow: 2px 2px 2px 2px #111111;
  cursor: pointer;
  flex-basis: 15em;
  flex-grow: 1;
  margin: 0 1em 1.5em 1em;
  position: relative;
  transition: all 0.2s ease-in-out;
}

.cards .card .card__header {
  padding: 1rem 0px 1rem .5rem;
}

.cards .card .card__header h4 {
  padding: 0;
  margin: 0;
}

.cards .card .card__header .icon {
  width: .6em;
  height: .6em;
  stroke: #a19f9e;
  fill: #a19f9e;
  margin: 0;
  padding: 0;
}

.cards .card .card__header time {
  font-size: .8rem;
  font-weight: lighter;
  padding: 0;
  margin: 0;
}

.cards .card .card__content {
  padding: .5rem;
}

.blog__pagination {
  padding: 2rem 0px 0px .5rem;
}

.blog__pagination a {
  padding: .5rem 1rem;
  border: thin solid #2a2a2a;
  border-radius: .5rem;
  background: url(data:image/svg+xml;base64,);
  background: linear-gradient(to bottom, #b0aeae, #bfbebd);
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.6), inset 0 1px rgba(255, 255, 255, 0.4);
  color: #2a2a2a;
  text-decoration: none;
  text-shadow: 0 1px 1px rgba(255, 255, 255, 0.72);
}

.blog__pagination a:hover {
  background: url("data:image/svg+xml;base64,");
  background: linear-gradient(to bottom, #0081c5, #006da6);
  color: #f6f6f6;
}

.blog__pagination .pagination__current {
  background: url("data:image/svg+xml;base64,");
  background: linear-gradient(to bottom, #005987, #004569);
  color: #f6f6f6;
}

.blog__pagination .pagination__current:hover {
  cursor: default;
  color: #f6f6f6;
}

.blog__pagination .pagination__previous:before {
  content: "« ";
  font-weight: normal;
}

.blog__pagination .pagination__next:after {
  content: " »";
  font-weight: normal;
}

.blog__pagination .pagination__next:hover, .blog__pagination .pagination__previous:hover {
  border-color: #2a2a2a;
  background: url("data:image/svg+xml;base64,");
  background: linear-gradient(to bottom, #33302e, #433f3c, 12%);
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.6), inset 0 1px rgba(255, 255, 255, 0.1);
  color: #f6f6f6;
  text-shadow: none;
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: #006da6;
  fill: #006da6;
}

.blog__tags a {
  margin: 1rem;
  display: inline-block;
}

.blog__tags .text {
  background: white;
  position: relative;
  padding: 0.25em 0.75em 0.25em 0.5em;
  margin: 0.5em 0px 0.5em 0.5em;
  border-radius: 3px 0 0 3px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  text-shadow: 0 1px 1px white;
  cursor: pointer;
  background: #ffffff;
  text-transform: uppercase;
  background: url(data:image/svg+xml;base64,);
  background: linear-gradient(to bottom, #b0aeae, #bfbebd);
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.6), inset 0 1px rgba(255, 255, 255, 0.4);
  color: #2a2a2a;
}

.blog__tags .count {
  color: white;
  text-shadow: 0 -1px rgba(0, 0, 0, 0.5);
  padding: 0.25em 0.75em 0.25em 0.5em;
  background: #006da6;
  border-radius: 0 3px 3px 0;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  margin: 0px 0px 0px -.5rem;
}

.blog__tags :hover {
  color: #f6f6f6;
}

.back-to-top {
  background: none;
  margin: 0;
  position: fixed;
  bottom: 0;
  right: 0;
  width: 70px;
  height: 70px;
  z-index: 100;
  display: none;
  text-decoration: none;
  opacity: 0.6;
}

#youtube {
  margin: 1rem 0;
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
}

#youtube iframe, #youtube object, #youtube embed {
  border: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
